import React, { useState } from "react";

import sendCorebizProducts from '../../Services/postCorebizAPI';

import styles from './style.module.scss';

export function PromotionForm() {
  const [disabled, setDisabled] = useState(true);
  const [userEmail, setEmail] = useState('');
  const [userName, setName] = useState('');
  const [renderNoRegister, setRenderNoRegister] = useState('');

  function handleChangeInput({ name, value }) {
    const patterns = {
      nameRegex: /\w+/ig,
      emailRegex: /^([a-z\d.-]+)@([a-z\d-]+)\.([a-z]{2,8})(\.[a-z]{2,8})?$/,
    };
    if (name === "email") {
      setDisabled(!(patterns.emailRegex.test(value) && patterns.nameRegex.test(userName)));
      setEmail(value)
    }

    if (name === "name") {
      setDisabled(!(patterns.emailRegex.test(userEmail) && patterns.nameRegex.test(value)));
      setName(value)
    }
  }

  const submitForm = () => {
  
    const infosToSend = {
      "email": userEmail,
      "name": userName
    };

    let requestOptions = {
      method: 'POST',
      body: infosToSend,
      redirect: 'follow'
    };

    sendCorebizProducts(requestOptions);
    setRenderNoRegister(true);
  }

  return (
    renderNoRegister ? (
      <section className={ styles.form__checked__container }>
        <p className={ styles.succes__main__msg }>
          Seu e-mail foi cadastrado com sucesso!
        </p>
        <p className={ styles.succes__detail__msg }>
          A partir de agora você receberá as novidades e ofertas exclusivas.
        </p>
        <input
          onClick={() => {
            setRenderNoRegister(false)
            setDisabled(true);
            setEmail('');
            setName('');
          }}
          className={ styles.promotion__btn }
          type="button"
          value="Cadastrar novo e-mail"
        />
      </section>
    ) : (
    <section  className={ styles.form__unchecked__container }>
      <h4 className={ styles.promotion__title }>
        Participe de nossas news com promoções e novidades!
      </h4>
      <form className={ styles.promotion__form }>
        <input
          onChange={({target}) => handleChangeInput(target)}
          className={ styles.promotion__input }
          name="name"
          type="text"placeholder="Digite seu nome"
        />
        <input
          onChange={({target}) => handleChangeInput(target)}
          className={ styles.promotion__input }
          name="email"
          type="email"
          placeholder="Digite seu email"
        />
        <input
          onClick={() => submitForm()}
          disabled={ disabled }
          className={ styles.promotion__btn }
          type="button"
          value="Eu quero!"
        />
      </form>
    </section>)
  );
}
